import { FormControl, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-types',
  templateUrl: './types.component.html',
  styleUrls: ['./types.component.css']
})
export class TypesComponent implements OnInit {

  public titleFormType: string;
  public nameType: FormControl;

  constructor(private ngbModal: NgbModal) { }

  ngOnInit(): void {
  }

  public openFormType(content: any, typeData: any): void {
    this.titleFormType = typeData ? "Editar tipo" : "Crear tipo";
    this.nameType = new FormControl("", [Validators.required, Validators.maxLength(20)]);
    this.ngbModal.open(content, {size: "lg"});
  }

}
